.link{background-repeat:no-repeat;background-image:linear-gradient(currentColor, currentColor),linear-gradient(currentColor, currentColor);background-size:51% 1px,50% 1px;background-position:0 95%,100% 95%;animation:underlineSlideIn .6s var(--ease) forwards;opacity:.5}.link:hover{background-position:210% 95%,300% 95%;animation:underlineSlideOut .6s var(--ease) forwards}@keyframes underlineSlideIn{0%{background-position:-300% 95%,-210% 95%}100%{background-position:0 95%,100% 95%}}@keyframes underlineSlideOut{0%{background-position:0 95%,100% 95%}100%{background-position:210% 95%,300% 95%}}