.row{z-index:0;display:grid;grid-template-columns:repeat(4, 1fr);grid-column:1/-1;gap:var(--grid-gap);padding:var(--row-margin) 0;border-bottom:1px solid var(--border-color)}.row.-fit{display:block;padding-bottom:unset;border:unset}.row.-flow{display:block;-moz-column-count:4;column-count:4;-moz-column-fill:balance;column-fill:balance;gap:var(--grid-gap);grid-auto-flow:dense;widows:3;orphans:3}.row.-flow p{margin-bottom:var(--row-margin)}.row.-flow p:last-child{margin-bottom:unset}@media screen and (max-width: 1023px){.row.-flow{-moz-column-count:2;column-count:2}}@media screen and (max-width: 643px){.row.-flow{-moz-column-count:auto;column-count:auto}}.row:last-child{border:none}.row .date{text-align:end}@media screen and (max-width: 1023px){.row{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 643px){.row{grid-template-columns:auto}}a.row .content-title h3{display:inline;background-repeat:no-repeat;background-image:linear-gradient(currentColor, currentColor),linear-gradient(currentColor, currentColor);background-size:51% 2px,50% 2px;background-position:-300% 100%,-210% 100%;transition:all .4s var(--ease)}a.row .content-title h3::after{content:" â†—";display:inline-block;transition:all .2s var(--ease);opacity:0}a.row:hover .content-title h3{background-position:0 100%,100% 100%}a.row:hover .content-title h3::after{opacity:1}@media screen and (max-width: 767px){a.row .content-title h3{background-position:0 100%,100% 100%}a.row .content-title h3::after{opacity:1}}