html{display:flex;background:var(--background-color)}body{display:flex;width:100%;color:var(--foreground-color);background:var(--background-color)}#root{width:100%}main,section{display:grid;grid-template-columns:repeat(8, 1fr);grid-column:1/-1;-moz-column-gap:var(--grid-gap);column-gap:var(--grid-gap)}@media screen and (max-width: 1023px){main,section{grid-template-columns:repeat(4, 1fr)}}@media screen and (max-width: 643px){main,section{grid-template-columns:repeat(2, 1fr)}}main{max-width:var(--body);padding:0 var(--margin);margin:0 auto}main hr{grid-column:1/-1}section{padding:var(--margin) 0;color:var(--foreground-color);background:var(--background-color)}section:first-child{padding-top:calc(var(--header)*2.5)}section:last-child{padding-bottom:calc(var(--header)*2.5)}section.content::before{content:"";position:absolute;top:0;left:50%;width:100vw;height:100%;background:var(--background-color);transform:translateX(-50%)}section.content .row{opacity:0;transform:translateY(2rem)}section.content .row.-on{animation:fadeIn 1s var(--ease) forwards}@keyframes fadeIn{from{opacity:0;transform:translateY(2rem)}to{opacity:1;transform:translateY(0)}}