.status{display:flex;align-items:center;flex-wrap:wrap;gap:0 .5em;color:var(--foreground-dimmed-color);font-size:14px;font-weight:500;line-height:1.5;letter-spacing:-0.01em}.status .dot{display:block;width:8px;height:8px;border-radius:50%}@keyframes pulse{0%{opacity:.4}50%{opacity:1}100%{opacity:.4}}.status .timer::before{content:"Seoul, "}.status .timer::after{content:attr(meridiem)}