.tagline{grid-column:1/-1;height:calc(var(--title-size)*1.25*3)}.tagline #quotes{padding:0 .125em;color:var(--foreground-dimmed-color);background:var(--foreground-tint-color);border-radius:.125em}.tagline .-accent{color:var(--foreground-color) !important}.tagline .cursor{width:0;outline:.075em solid var(--foreground-color)}.tagline .cursor.-blink{animation:blink .8s infinite}@keyframes blink{50%{opacity:0}}@media screen and (max-width: 643px){.tagline{height:calc(var(--title-size)*1.25*4)}}@media screen and (max-width: 424px){.tagline{height:calc(var(--title-size)*1.25*5)}}